<template>
  <div class="call">
    <div>
      <marquee behavior="alternate" scrollamount="3" v-html="marqueeText"></marquee>
    </div>
    <div class="inputWarp">
      <el-row :gutter="20">
        <el-col :span="12" v-for="(item, index) in todayList" :key="index" class="itemTodayIfon">
          <el-input placeholder="" v-model="item.val" disabled>
            <template slot="prepend">{{ item.name }}</template>
          </el-input>
        </el-col>
        <el-col :span="12" class="itemTodayIfon">
          <el-input placeholder="输入申请方" v-model="applyName" :disabled="openApplyName">
            <template slot="prepend">申请方</template>
          </el-input>
        </el-col>
        <el-col :span="12" class="itemTodayIfon">
          <el-input placeholder="输入申请项目" v-model="applyObject" :disabled="openApplyObject">
            <template slot="prepend">申请项目</template>
          </el-input>
        </el-col>
      </el-row>
    </div>
    <div class="btnWarp">
      <el-row :gutter="20">
        <el-col :span="8" class="itemBtnWarp">
          <el-button class="itemBtn" type="success" @click="nextPersonFn" :disabled="nextPerson.disabled"><svg-icon :icon-class="nextPerson.icon" style="width: 30px;height: 30px;"></svg-icon>{{ nextPerson.name }}</el-button>
        </el-col>
        <el-col :span="8" class="itemBtnWarp">
          <el-button class="itemBtn" type="warning" @click="reCallFn" :disabled="reCall.disabled"><svg-icon :icon-class="reCall.icon" style="width: 30px;height: 30px;"></svg-icon>{{ reCall.name }}</el-button>
        </el-col>
        <el-col :span="8" class="itemBtnWarp">
          <el-button class="itemBtn" type="danger" @click="pauseFn" :disabled="pause.disabled"><svg-icon :icon-class="pause.icon" style="width: 30px;height: 30px;"></svg-icon>{{ pause.name }}</el-button>
        </el-col>
        <el-col :span="8" class="itemBtnWarp">
          <el-button class="itemBtn" type="danger" @click="moveFn" :disabled="move.disabled"><svg-icon :icon-class="move.icon" style="width: 30px;height: 30px;"></svg-icon>{{ move.name }}</el-button>
        </el-col>
        <el-col :span="8" class="itemBtnWarp">
          <el-button class="itemBtn" type="info" @click="AcceptanceFn" :disabled="!openAcceptance"><svg-icon :icon-class="Acceptance.icon" style="width: 30px;height: 30px;"></svg-icon>{{ Acceptance.name }}</el-button>
        </el-col>
        <el-col :span="8" class="itemBtnWarp">
          <el-button class="itemBtn" type="primary" @click="unAcceptanceFn" :disabled="unAcceptance.disabled"><svg-icon :icon-class="unAcceptance.icon" style="width: 30px;height: 30px;"></svg-icon>{{ unAcceptance.name }}</el-button>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import lodash from 'lodash'
export default {
  name: 'call',
  data () {
    return {
      userIfon: {},
      marqueeText: '',
      initialMarqueeText: '',
      todayList: [
        {
          name: '当前窗口号',
          val: localStorage.window
        },
        {
          name: '当前办理号',
          val: ''
        },
        {
          name: '等待人数',
          val: ''
        },
        {
          name: '当天办理总数',
          val: ''
        }
      ],
      applyName: '',
      openApplyName: true,
      applyObject: '',
      openApplyObject: true,
      nextPerson: {
        name: '下一位',
        icon: 'user',
        disabled: false
      },
      reCall: {
        name: '重新呼叫',
        icon: 'horn',
        disabled: true
      },
      pause: {
        name: '暂停',
        icon: 'pause',
        disabled: false
      },
      isPause: true,
      move: {
        name: '转号',
        icon: 'move',
        disabled: true
      },
      Acceptance: {
        name: '受理',
        icon: 'Acceptance',
        disabled: true
      },
      unAcceptance: {
        name: '未受理',
        icon: 'unAcceptance',
        disabled: true
      },
      personInfo: {}
    }
  },
  computed: {
    openAcceptance: function () {
      return (this.applyName.replace(/(^\s*)|(\s*$)/g, '') !== '' && this.applyObject.replace(/(^\s*)|(\s*$)/g, '') !== '')
    }
  },
  watch: {
    openAcceptance (val) {
      if (val) {
        this.Acceptance.disabled = false
      } else {
        this.Acceptance.disabled = true
      }
    }
  },
  created: function () {
    if (sessionStorage.userInfo) {
      this.userIfon = JSON.parse(sessionStorage.userInfo)
      this.marqueeText = '欢迎 <b>' + this.userIfon.name + '</b> 登录系统'
      this.initialMarqueeText = this.marqueeText
    } else {
      console.log('call-无用户信息')
    }
  },
  methods: {
    // 点击事件：600毫秒内，以最后的为准
    nextPersonFn: lodash.debounce(function () {
      this.callNextPersonFn()
    }, 1000),
    reCallFn: lodash.debounce(function () {
      this.reCallHandle()
    }, 1000),
    pauseFn: lodash.debounce(function () {
      if (this.isPause) { // 暂停
        this.pause = {
          name: '开始',
          icon: 'start',
          disabled: false
        }
        this.pauseHandleFn()
        let list = [
          true, true, false,
          true, true, true
        ]
        this.distributeDisabled(list, this)
      } else { // 开始
        this.pause = {
          name: '暂停',
          icon: 'pause',
          disabled: false
        }
        this.startHandleFn()
        let list = [
          false, true, false,
          true, true, true
        ]
        this.distributeDisabled(list, this)
      }
      this.isPause = !this.isPause
    }, 5),
    moveFn: lodash.debounce(function () {
      let reference = JSON.parse(sessionStorage.reference)
      console.log(reference)
      console.log(this.userIfon)
      if (reference.radio === '3') {
        if (reference.curTheme === '') {
          this.$alert('请选择转号的主题', '提示', {
            confirmButtonText: '确定',
            callback: action => {
            }
          })
        } else if (reference.curMatter === '') {
          this.$alert('请选择转号的事项', '提示', {
            confirmButtonText: '确定',
            callback: action => {
            }
          })
        } else if (reference.curTheme === this.userIfon.subject) {
          this.$alert('转号的主题与工作人员所在主题一致，请修改', '提示', {
            confirmButtonText: '确定',
            callback: action => {
            }
          })
        } else { // 转号
          this.$confirm('事件将转移到' + reference.curTheme + '-' + reference.curMatter, '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            this.moveHandleFn(reference)
          }).catch(() => {
          })
        }
      } else {
        this.$alert('请选择转号功能', '提示', {
          confirmButtonText: '确定',
          callback: action => {
          }
        })
      }
    }, 600),
    AcceptanceFn: lodash.debounce(function () {
      // let referenceObj = JSON.parse(sessionStorage.reference)
      // if (referenceObj.selectionElementId.length) {
      //   if (referenceObj.selectionElementId.length < referenceObj.allID.length) {
      //     this.myAlert('审查要素未核对完', '提示')
      //   } else {
      //     let reference = JSON.parse(sessionStorage.reference)
      //     if (reference.radio === '2') {
      //       if (reference.curTheme) {
      //         if (reference.curMatter) {
      //           if (this.userIfon.subject === reference.curTheme) {
      //             this.$confirm('请确定当前事项合格', '提示', {
      //               confirmButtonText: '确定',
      //               cancelButtonText: '取消',
      //               type: 'warning'
      //             }).then(() => {
      //               this.personInfo.subject = reference.curTheme
      //               this.personInfo.item = reference.curMatter
      //               this.personInfo.deal_status = 'Y'
      //               this.AcceptanceHandleFn()
      //             }).catch(() => {
      //               console.log('取消受理')
      //             })
      //           } else {
      //             this.myAlert('修改的主题与工作人员所在主题不一致，请修改', '提示')
      //           }
      //         } else {
      //           this.myAlert('未选择需修改的事项', '提示')
      //         }
      //       } else {
      //         this.myAlert('未选择需修改的主题', '提示')
      //       }
      //     } else {
      //       this.$confirm('请确定当前事项合格', '提示', {
      //         confirmButtonText: '确定',
      //         cancelButtonText: '取消',
      //         type: 'warning'
      //       }).then(() => {
      //         this.personInfo.deal_status = 'Y'
      //         this.AcceptanceHandleFn()
      //       }).catch(() => {
      //         console.log('取消受理')
      //       })
      //     }
      //     // this.$alert('请确定当前事项合格', '提示', {
      //     //   confirmButtonText: '确定',
      //     //   callback: action => {
      //     //     let reference = JSON.parse(sessionStorage.reference)
      //     //     if (reference.radio === '2') {
      //     //       this.personInfo.subject = reference.curTheme
      //     //       this.personInfo.item = reference.curMatter
      //     //     }
      //     //     this.personInfo.deal_status = 'Y'
      //     //     this.AcceptanceHandleFn()
      //     //   }
      //     // })
      //   }
      // } else {
      //   this.myAlert('请核对审查要素', '提示')
      // }
      let reference = JSON.parse(sessionStorage.reference)
      if (reference.radio === '2') {
        if (reference.curTheme) {
          if (reference.curMatter) {
            if (this.userIfon.subject === reference.curTheme) {
              this.$confirm('请确定当前事项合格', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
              }).then(() => {
                this.personInfo.subject = reference.curTheme
                this.personInfo.item = reference.curMatter
                this.personInfo.deal_status = 'Y'
                this.AcceptanceHandleFn()
              }).catch(() => {
                console.log('取消受理')
              })
            } else {
              this.myAlert('修改的主题与工作人员所在主题不一致，请修改', '提示')
            }
          } else {
            this.myAlert('未选择需修改的事项', '提示')
          }
        } else {
          this.myAlert('未选择需修改的主题', '提示')
        }
      } else {
        this.$confirm('请确定当前事项合格', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.personInfo.deal_status = 'Y'
          this.AcceptanceHandleFn()
        }).catch(() => {
          console.log('取消受理')
        })
      }
    }, 600),
    unAcceptanceFn: lodash.debounce(function () {
      let reference = JSON.parse(sessionStorage.reference)
      if (reference.radio === '2') {
        if (reference.curTheme) {
          if (reference.curMatter) {
            if (this.userIfon.subject === reference.curTheme) {
              this.$confirm('请确定当前事项不合格', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
              }).then(() => {
                this.personInfo.subject = reference.curTheme
                this.personInfo.item = reference.curMatter
                this.personInfo.deal_status = 'N'
                this.AcceptanceHandleFn()
              }).catch(() => {
                console.log('取消未受理')
              })
            } else {
              this.myAlert('修改的主题与工作人员所在主题不一致，请修改', '提示')
            }
          } else {
            this.myAlert('未选择需修改的事项', '提示')
          }
        } else {
          this.myAlert('未选择需修改的主题', '提示')
        }
      } else {
        this.$confirm('请确定当前事项不合格', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.personInfo.deal_status = 'N'
          this.AcceptanceHandleFn()
        }).catch(() => {
          console.log('取消未受理')
        })
      }
      // this.$alert('请确定当前事项不合格', '提示', {
      //   confirmButtonText: '确定',
      //   callback: action => {
      //     let reference = JSON.parse(sessionStorage.reference)
      //     if (reference.radio === '2') {
      //       if (reference.curTheme) {
      //         if (reference.curMatter) {
      //           if (this.userIfon.subject === reference.curTheme) {
      //             this.personInfo.subject = reference.curTheme
      //             this.personInfo.item = reference.curMatter
      //             this.personInfo.deal_status = 'N'
      //             this.AcceptanceHandleFn()
      //           } else {
      //             this.myAlert('无权修改到其他主题下', '提示')
      //           }
      //         } else {
      //           this.myAlert('未选择需修改的事项', '提示')
      //         }
      //       } else {
      //         this.myAlert('未选择需修改的主题', '提示')
      //       }
      //     } else {
      //       this.personInfo.deal_status = 'N'
      //       this.AcceptanceHandleFn()
      //     }
      //   }
      // })
    }, 600),
    async callNextPersonFn () {
      try {
        let params = this.userIfon
        let res = await this.$api.unsplash.getNextPerson(params)
        console.log(res)
        if (res.errno === '0') {
          // 有人就放数据
          this.personInfo = res.data.business
          this.personInfo.user_id = this.userIfon.user_id
          this.personInfo.lingpai = localStorage.lingpai
          this.todayList[1].val = this.personInfo.number
          this.todayList[2].val = res.data.wait
          this.todayList[3].val = res.data.called
          this.applyName = ''
          this.applyObject = ''
          this.openApplyName = false
          this.openApplyObject = false
          if (this.personInfo.cometimes + 1 >= 3) {
            this.marqueeText = `流水号:<b>` + this.personInfo.water_num + `</b>，本次为<b style="color:red;">第` + (this.personInfo.cometimes + 1) + `次</b>来办理<b>` + this.personInfo.item + `</b>，<b style="color:red;">请认真对待每位群众</b>`
          } else {
            this.marqueeText = `流水号:<b>` + this.personInfo.water_num + `</b>，本次为<b>第` + (this.personInfo.cometimes + 1) + `次</b>来办理<b>` + this.personInfo.item + `</b>，<b style="">请认真对待每位群众</b>`
          }
          // 初始化参考各项
          this.getElement(this.personInfo.item)
          // 按钮锁
          let list = [
            true, false, true,
            false, true, false
          ]
          this.distributeDisabled(list, this)
          let referenceObj = JSON.parse(sessionStorage.reference)
          referenceObj.filteredList = []
          sessionStorage.reference = JSON.stringify(referenceObj)
        } else if (res.errno === '4004') {
          this.todayList[1].val = ''
          this.notifyFn('提示', res.errmsg)
          // 按钮锁
          let list = [
            false, true, false,
            true, true, true
          ]
          this.distributeDisabled(list, this)
        } else {
          this.todayList[1].val = ''
          this.notifyFn('提示', res.errmsg)
          // 按钮锁
          let list = [
            false, true, false,
            true, true, true
          ]
          this.distributeDisabled(list, this)
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        // 按钮锁
        let list = [
          false, true, false,
          true, true, true
        ]
        this.distributeDisabled(list, this)
        console.log(e)
      }
    },
    async getElement (curMatter) {
      try {
        let params = {
          item: curMatter
        }
        let res = await this.$api.unsplash.postElements(params)
        if (res.errno === '0') {
          let temp = res.data
          let allID = []
          for (let i = 0; i < temp.length; i++) {
            temp[i].index = i + 1
            allID.push(temp[i].id)
          }
          let reference = {
            radio: '1',
            curTheme: this.personInfo.subject,
            curMatter: this.personInfo.item,
            allID: allID,
            selectionElementId: this.personInfo.last_reviews ? this.personInfo.last_reviews.split('-') : []
          }
          sessionStorage.reference = JSON.stringify(reference)
        } else {
          this.notifyFn('提示', res.errmsg)
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    async reCallHandle () {
      try {
        let params = {
          water_num: this.personInfo.water_num,
          lingpai: localStorage.lingpai,
          user_id: sessionStorage.user_id
        }
        let res = await this.$api.unsplash.postReCall(params)
        if (res.errno === '0') {
          this.notifyFn('提示', '正在重呼', 'success')
        } else {
          this.notifyFn('提示', res.errmsg)
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    async pauseHandleFn () {
      try {
        let params = {
          lingpai: localStorage.lingpai,
          user_id: sessionStorage.user_id
        }
        let res = await this.$api.unsplash.postPause(params)
        if (res.errno === '0') {
          this.afterDealed(this)
        } else {
          this.notifyFn('提示', res.errmsg)
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    async startHandleFn () {
      try {
        let params = {
          lingpai: localStorage.lingpai,
          user_id: sessionStorage.user_id
        }
        let res = await this.$api.unsplash.postStart(params)
        if (res.errno === '0') {
          this.afterDealed(this)
        } else {
          this.notifyFn('提示', res.errmsg)
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    async moveHandleFn (reference) {
      try {
        let params = this.personInfo
        params.subject = reference.curTheme
        params.item = reference.curMatter
        console.log(this.personInfo)
        let res = await this.$api.unsplash.postMove(params)
        console.log(res)
        if (res.errno === '0') {
          this.afterDealed(this)
          let list = [
            false, true, false,
            true, true, true
          ]
          this.distributeDisabled(list, this)
          this.marqueeText = this.initialMarqueeText
        } else {
          this.notifyFn('提示', res.errmsg)
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    async AcceptanceHandleFn () {
      try {
        let referenceObj = JSON.parse(sessionStorage.reference)
        this.personInfo.reviews = referenceObj.selectionElementId.join('-')
        this.personInfo.p_name = this.applyName.replace(/(^\s*)|(\s*$)/g, '')
        this.personInfo.p_project = this.applyObject.replace(/(^\s*)|(\s*$)/g, '')
        let params = this.personInfo
        console.log(14, this.personInfo)
        let res = await this.$api.unsplash.postAcceptance(params)
        console.log(res)
        if (res.errno === '0') {
          this.afterDealed(this)
          let list = [
            false, true, false,
            true, true, true
          ]
          this.distributeDisabled(list, this)
          this.marqueeText = this.initialMarqueeText
        } else {
          this.notifyFn('提示', res.errmsg)
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    notifyFn (title, msg, type = 'warning') {
      this.$notify({
        title: title,
        message: msg,
        type: type,
        dangerouslyUseHTMLString: true,
        duration: 5000,
        position: 'top-right'
      })
    },
    myAlert (txt, title) {
      this.$alert(txt, title, {
        confirmButtonText: '确定',
        callback: action => {
        }
      })
    },
    distributeDisabled (list, self) {
      self.nextPerson.disabled = list[0]
      self.reCall.disabled = list[1]
      self.pause.disabled = list[2]
      self.move.disabled = list[3]
      self.Acceptance.disabled = list[4]
      self.unAcceptance.disabled = list[5]
    },
    afterDealed (self) {
      self.todayList[1].val = ''
      self.applyName = ''
      self.openApplyName = true
      self.applyObject = ''
      self.openApplyObject = true
    }
  }
  // beforeRouteLeave (to, from, next) {
  //   // 设置下一个路由的 meta
  //   // to.meta.keepAlive = true // 不刷新
  //   // next()
  // }
}
</script>

