        font-size: 48px;
        font-weight: bold;
        color: black;
        border-color: $btnColorAfter;
        background: $btnColorAfter;
      }
      .el-input__inner::-webkit-input-placeholder{
        color: #74767a;
        font-size: 20px;
        vertical-align: middle;
      }
      .el-input__inner:-moz-placeholder{
        color: #74767a;
        font-size: 20px;
        vertical-align: middle;
      }
      .el-input__inner::-moz-placeholder{
        color: #74767a;
        font-size: 20px;
        vertical-align: middle;
      }
      .el-input__inner:-ms-input-placeholder{
        color: #74767a;
        font-size: 20px;
        vertical-align: middle;
      }
    }
  }
  .btnWarp{
    width: 1200px;
    /*height: 800px;*/
    margin: 0 auto;
    padding: 40px;
    /*background: #ffae7e;*/
    .itemBtnWarp{
      height: 100px;
      margin-bottom: 20px;
      &:nth-last-child(1),&:nth-last-child(2){
        margin-bottom: 0;
      }
      .itemBtn{
        width: 100%;
        height: 100%;
        font-size: 30px;
        /*font-weight: bold;*/
        color: #ffffff;
      }
    }
  }
</style>



